<template>
	<div class="download-excel-list" v-title data-title="导出记录">
		<Nav @back="back" title="导出记录" :hasShadow="true" backColor="#B6CFDE">
			<div slot="right" style="width: 246px">
				<!-- 布局占位 -->
			</div>
		</Nav>

		<div class="main">
			<div class="search-box">
				<div class="search-box-left">
					<CustomSelect margin="0 15px 0 0">
						<template slot="select-container">
							<el-select v-model="category" placeholder="全部类型" @change="change">
								<el-option v-for="(item, index) in categoryList" :key="index" :label="item.name" :value="item.exportTasktName">
									<!-- <span >{{ item.areaName }}</span> -->
								</el-option>
							</el-select>
						</template>
					</CustomSelect>

					<CustomSelect margin="0 15px 0 0">
						<template slot="select-container">
							<el-select v-model="exportFlag" placeholder="全部导出状态" @change="change">
								<el-option v-for="(item, index) in downloadStatusList" :key="index" :label="item.text" :value="item.status">
									<!-- <span >{{ item.areaName }}</span> -->
								</el-option>
							</el-select>
						</template>
					</CustomSelect>

					<!-- <CustomInput iconfont="el-icon-search" placeholder="输入经营主体名称搜索" v-model="keyword" @clickAndEnter="clickAndEnter"></CustomInput> -->
				</div>
				<div class="excel-box">
					<!-- <div class="excel" @click="download">
						<img src="../../assets/images/table/bg-excel-icon.png" />
						<span style="margin-left: 9px">导出EXCEL</span>
					</div> -->
					<div class="down-notice">
						<i class="el-icon-s-opportunity"></i>
						<span>文件导出，系统需要约5分钟处理，请耐心等待，已经生成的报表最多保留60天。</span>
					</div>
				</div>
			</div>
			<div class="list-content" ref="listContent">
				<!-- <Loading :show="true" type="element"></Loading> -->
				<CustomTable>
					<template slot="table-container">
						<el-table :data="downloadList" style="width: 100%" :fit="true" :height="tableHeight" :lazy="true">
							<template slot="empty">
								<div>
									<Loading :show="loading" type="element" theme="loader-light"></Loading>
									<div class="no-data" v-if="!loading">
										<img src="../../assets/images/table/bg-table-no-data.png" />
										<span>暂无数据</span>
									</div>
								</div>
							</template>
							<el-table-column label="文件名称" width="600">
								<template slot-scope="scope">
									<span class="text-bold">{{ scope.row.exportName }}</span>
								</template>
							</el-table-column>

							<el-table-column label="导出对象类型">
								<template slot-scope="scope">
									<span class="text">{{ getTaskName(scope.row.taskName) }}</span>
								</template>
							</el-table-column>

							<el-table-column label="导出状态">
								<template slot-scope="scope">
									<span class="text" :style="{color: getColor(scope.row.exportFlag)}">{{ downloadStatusList[scope.row.exportFlag + 1].text }}</span>
								</template>
							</el-table-column>

							<el-table-column label="导出数据量（条)">
								<template slot-scope="scope">
									<span class="text-bold">{{ scope.row.exportNum ? scope.row.exportNum : '--' }}</span>
								</template>
							</el-table-column>

							<el-table-column label="导出时间">
								<template slot-scope="scope">
									<span class="text">{{ formatDate(scope.row.createTime, '.') }}</span>
								</template>
							</el-table-column>

							<el-table-column label="导出结果" width="200">
								<template slot-scope="scope">
									<div class="handle" v-if="scope.row.exportFlag === 1">--</div>
									<div class="handle handle-success" v-if="scope.row.exportFlag === 2">
										<a :href="scope.row.filePath" :download="scope.row.exportName">下载查看</a>
										<span class="line"></span>
										<span @click="handleEdit(scope.row)">删除</span>
									</div>
									<div class="handle" v-if="scope.row.exportFlag === 0 || scope.row.exportFlag === 3">
										<span @click="handleEdit(scope.row)">删除</span>
									</div>
								</template>
							</el-table-column>
						</el-table>
					</template>
				</CustomTable>
			</div>
		</div>

		<div v-if="total > pageSize" class="page-box">
			<Page
				:total="total"
				:page-size="pageSize"
				:current="currentPage"
				@onChange="onChange"
				@changePageSize="changePageSize"
				:isPageSizeShow="true"
				:isJumpShow="true"
				:isTotalShow="true"
				:perPage="15"
			></Page>
		</div>
	</div>
</template>

<script>
import Nav from '@/components/common/nav.vue';
import Page from '@/components/common/page.vue';
import Loading from '@/components/common/loading.vue';
import CustomInput from '@/components/common/input.vue';
import CustomSelect from '@/components/common/select.vue';
import CustomTable from '@/components/common/table.vue';
import CustomTableSort from '@/components/common/custom-table-sort.vue';

import {areaLevelCorAreaId} from '@/common/fn/index.js';
import {SessionStorage, getStorage} from '@/utils/storage.js';
import filterArea from '@/common/config/filterArea.js';
import {downloadExcel} from '@/utils/utils.js';

export default {
	data() {
		return {
			currentPage: 1,
			pageSize: 15,
			total: 1,
			downloadList: [],
			sortDirection: null,
			defaultSort: 'descending',
			tableHeight: 0,
			loading: false,
			downloadStatusList: [
				{
					status: 100,
					text: '全部导出',
				},
				{
					status: 0,
					text: '等待导出',
				},
				{
					status: 1,
					text: '导出中',
				},
				{
					status: 2,
					text: '导出成功',
				},
				{
					status: 3,
					text: '导出失败',
				},
			],
			exportFlag: 100,
			categoryList: [
				{
					exportTasktName: '',
					name: '全部导出对象',
				},
				{
					exportTasktName: 'TASKPATROLRECORD',
					name: '安全监管',
				},
				{
					exportTasktName: 'TASKLVSELANDCERTIFICATE',
					name: '绿色认证',
				},
				{
					exportTasktName: 'TASKFARMBUSINESS',
					name: '经营主体',
				},
				// {
				// 	exportTasktName: 'TASKZZBASCOMPANY',
				// 	name: '种植业经营主体'
				// },
				// {
				// 	exportTasktName: 'TASKCROPGROUPAREA',
				// 	name: '在田统计'
				// },
				{
					exportTasktName: 'TASKFARMOPERATIONREPORT',
					name: '生产作业上报',
				},
			],
			category: '',
		};
	},
	created() {
		this.getDownloadList();
		// this.getArea();
	},
	// beforeRouteEnter(to, from, next) {
	// 	// console.log('from==========>', from);
	// 	if (from.path !== '/downloadExcelList') {
	// 		SessionStorage.set('storageRouter', {name: from.name});
	// 	}
	// 	next();
	// },
	mounted() {
		this.$nextTick(() => {
			this.tableHeight = this.$refs.listContent.offsetHeight;
		});
		window.addEventListener('resize', this.resizeHandle, false);
	},
	methods: {
		back() {
			var tempPath = this.$route.path;
			this.$router.go(-1);
			setTimeout(() => {
				if (tempPath === this.$route.path) {
					this.back();
				}
			}, 200);
		},
		change(val) {
			this.currentPage = 1;
			this.getDownloadList();
		},
		onChange(page) {
			this.currentPage = page;
			this.getDownloadList();
		},
		changePageSize(pageSize) {
			this.pageSize = pageSize;
			this.currentPage = 1;
			this.getDownloadList();
		},
		async handleEdit(row) {
			const res = await this.$ajax.post(this.$api.downloadExcel.downloadDel, {
				id: row.id,
			});

			if (res.data.respCode === 0) {
				this.$message({
					type: 'success',
					message: '删除成功',
					center: true,
				});

				// 删除一条数据后，当当前页在最后一页时，总条数取余每页条数余1时，说明最后一页数据删除完，最后一页向前减一页，避免出现最后一页没有数据
				if (this.total % this.pageSize === 1 && this.currentPage !== 1 && this.currentPage === Math.ceil(this.total / this.pageSize)) {
					this.currentPage -= 1;
				}

				this.getDownloadList();
			}
		},
		async getDownloadList() {
			this.downloadList = [];
			this.loading = true;
			const defaultArea = areaLevelCorAreaId();
			const userInfo = getStorage('govGovernmentUserVO');

			let options = {
				createBy: userInfo.id,
				exportTasktName: this.category,
				// lookFlag: 0,
				exportFlag: this.exportFlag === 100 ? '' : this.exportFlag,
				pageNo: this.currentPage,
				pageSize: this.pageSize,
			};

			if (!this.category) delete options.exportTasktName;

			const res = await this.$ajax.post(this.$api.downloadExcel.downloadExcelList, options);

			if (res.data.respCode === 0) {
				this.downloadList = res.data.obj.list;
				this.total = res.data.obj.totalCount;

				this.loading = false;

				this.$nextTick(() => {
					this.tableHeight = this.$refs.listContent.offsetHeight;
					this.backTop();
				});
			} else {
				this.loading = false;
			}
		},
		getColor(status) {
			switch (status) {
				case 0:
					return '#0E5B69';
					break;
				case 1:
					return '#945B1D';
					break;
				case 2:
					return '#1D9425';
					break;
				case 3:
					return '#B73A31';
					break;
				default:
					break;
			}
		},
		getTaskName(taskName) {
			if (taskName === 'TASKZZBASCOMPANY') {
				return '经营主体';
			}
			const filterData = this.categoryList.filter(ele => ele.exportTasktName === taskName);
			// console.log('filterData======>', filterData);
			return filterData.length ? filterData[0].name : '--';
		},
		formatDate(time, character) {
			const date = new Date(time);

			const YY = date.getFullYear() + character;
			const MM = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + character;
			const DD = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
			const HH = date.getHours() + ':';
			const MIN = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes();

			return YY + MM + DD + ' ' + HH + MIN;
		},
		resizeHandle() {
			this.tableHeight = this.$refs.listContent.offsetHeight;
		},
		backTop() {
			document.querySelector('.el-table__body-wrapper').scrollTop = 0;
		},
	},
	destroyed() {
		window.removeEventListener('resize', this.resizeHandle, false);
	},
	components: {
		Nav,
		Page,
		Loading,
		CustomInput,
		CustomSelect,
		CustomTable,
		CustomTableSort,
	},
};
</script>

<style scoped lang="scss">
.download-excel-list {
	overflow: hidden;
	display: flex;
	flex-direction: column;
	height: 100vh;
	min-width: 1366px;
	min-height: 696px;
	box-sizing: border-box;
	background-color: #021220;

	.main {
		flex: 1;
		display: flex;
		flex-direction: column;
		height: 100%;
		box-sizing: border-box;
		overflow: hidden;

		.search-box {
			display: flex;
			justify-content: space-between;
			padding: 28px 50px 21px;

			.search-box-left {
				flex: 1;
				display: flex;
			}

			.excel-box {
				.excel {
					display: flex;
					align-items: center;
					justify-content: center;
					width: 120px;
					height: 36px;
					border: 1px solid #145763;
					border-radius: 2px;
					background-color: #24969a;
					color: #d9e3ed;
					font-size: 14px;
					cursor: pointer;
				}

				.down-notice {
					display: flex;
					align-items: center;
					justify-content: center;
					height: 100%;
					color: #a75c22;
					font-size: 14px;

					i {
						margin-right: 8px;
						font-size: 16px;
					}
				}
			}
		}

		.list-content {
			flex: 1;
			display: flex;
			flex-direction: column;
			overflow: hidden;
			height: 100%;
		}
	}

	.handle {
		display: flex;
		align-items: center;
		font-size: 14px;
		color: #1fa8f8;

		&.handle-success {
			display: flex;
			align-items: center;
			a {
				color: #1fa8f8;
			}
			span:nth-child(1) {
				display: inline-block;
			}

			.line {
				display: inline-block;
				height: 12px;
				width: 1px;
				margin: 0 15px;
				background-color: #113556;
			}
		}
		span {
			cursor: pointer;
		}
	}
	.handle-disabled {
		color: #aaaaaa;
		cursor: not-allowed;
	}

	.text {
		color: #a1acb7;
		font-size: 14px;
	}
	.text-bold {
		color: #a1acb7;
		font-weight: bold;
		font-size: 14px;
	}
	.value-text {
		color: #082238;
		font-weight: bold;
		font-size: 14px;
	}
	.text-hover {
		color: #1d9096;
		cursor: pointer;
	}
	.text-hover:hover {
		border-bottom: 1px solid #1d9096;
	}

	.page-box {
		display: flex;
		justify-content: flex-end;
		align-items: center;
		height: 88px;
		padding: 0 40px;
		background: #021220;
		box-shadow: 0px -3px 21px 0px rgba(4, 52, 82, 0.2);
	}

	.no-data {
		display: flex;
		flex-direction: column;
		align-items: center;
	}
}
</style>

<style lang="scss">
.download-excel-list > .el-loading-mask {
	i {
		color: #00e5ff;
		font-size: 40px;
	}
	p {
		color: #00e5ff;
		line-height: 40px;
		font-size: 18px;
	}
}
.download-excel-list .custom-select {
	.el-input__inner::-webkit-input-placeholder {
		color: #072138;
	}
	.el-input__inner::-moz-placeholder {
		/* Mozilla Firefox 19+ */
		color: #072138;
	}
	.el-input__inner:-moz-placeholder {
		/* Mozilla Firefox 4 to 18 */
		color: #072138;
	}
	.el-input__inner:-ms-input-placeholder {
		/* Internet Explorer 10-11 */
		color: #072138;
	}
}
</style>



